<template>
    <div>
    	<br><el-button type="primary" @click="createUser">添加用户</el-button><br><br>
    	<el-dialog title="提示" :visible.sync="user.addUser" :width="this.$store.state.modalWidth" :before-close="handleClose">

			<el-form status-icon :label-position="this.$store.state.panelLayout" ref="ruleForm" :model="user" :rules="rules">
				<el-form-item label="用户名" :label-width="user.labelWidth" prop="userName">
					<el-input v-model="user.userName" :placeholder="user.placeholderUserName"></el-input>
				</el-form-item>
				<el-form-item label="密码" :label-width="user.labelWidth" prop="passWord">
					<el-input v-model="user.passWord" type="password" :placeholder="user.placeholderUserPassword" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="再次密码" :label-width="user.labelWidth" prop="checkPassWord">
					<el-input v-model="user.checkPassWord" type="password" :placeholder="user.placeholderUserPassword" auto-complete="off"></el-input>
				</el-form-item>

				<el-form-item label="选择级别码" :label-width="user.labelWidth" prop="leveldefault">
					<el-select v-model="user.leveldefault" placeholder="请选择">
					    <el-option
					      v-for="item in user.level"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					</el-select>
				</el-form-item>

				<el-form-item label="添加权限" :label-width="user.labelWidth" prop="jurisdiction">
					<!-- <el-tree class="eltree" :data="menudata" show-checkbox node-key="id" :default-expand-all="false" :expand-on-click-node="false"> -->
						<el-tree v-if="menudata" ref="treeid" highlight-current :data="menudata" default-expand-all show-checkbox node-key="id" :props="defaultProps" @check-change="getJurisdiction"> </el-tree>

				</el-form-item>

			</el-form>

			<span slot="footer" class="dialog-footer">
				<el-button @click="handleClose">取 消</el-button>
				<el-button type="primary" @click="submitUser">确 定</el-button>
			</span>

		</el-dialog>
    </div>
</template>

<script>
export default {
	components: {
    },
	data () {
		var validatePass = (rule, value, callback) => {
			if (value === '') {
			  callback(new Error('请输入密码'));
			} else {
			  if (this.user.checkPassWord !== '') {
			    this.$refs.ruleForm.validateField('checkPassWord');
			  }
			  callback();
			}
		};
		var validatePass2 = (rule, value, callback) => {
			if (value === '') {
			  callback(new Error('请再次输入密码'));
			} else if (value !== this.user.passWord) {
			  callback(new Error('两次输入密码不一致!'));
			} else {
			  callback();
			}
		};
		return {
			user: {
				addUser: false,
				placeholderUserName: '请输入用户名',
				placeholderUserPassword: '请输入用户密码',
				labelWidth: '120px',
				userName: '',
				passWord: '',
				checkPassWord: '',
				jurisdiction: '',
				user_menu_jurisdiction_set: null,
				level: [
					{
						value: 1,
						label: '超级管理员'
					},
					{
						value: 2,
						label: '亚普通管理员'
					},
					{
						value: 3,
						label: '季普通管理员'
					}
				],
				leveldefault: 2,
				ua: navigator.userAgent

			},
			filterText: '',
			formWidth: '50%',
			menudata: null,
			defaultProps: {
	          children: 'children',
	          label: 'label'
	        },
	        menuTreeData:{
		        name:'menu_name',
		        title:'menu_name',
		        href:'menu_href',
		        icon:'menu_icon',
		        parentId:'cid',
		        hrefP: 'menu_href'
		    },
			rules: {
				userName: [
					{required: true,message: '请输入用户名'}
				],
				leveldefault: [
					{required: true,message: '请选择级别'}
				],
				passWord: [
					{required: true, validator: validatePass}
				],
				checkPassWord: [
					{required: true, validator: validatePass2}
				]
			}
		}
	},
	created () {
		// var array_bar = ['a','b','c','d','e','f','g']
		// var array_foo = ['1','2','3','4','5','6','7','8','9']
		// var arr = array_bar.forEach((item, index) => {
		//     return array_foo.splice(1 * (index + 1) + index, 0, item);
		// });
		// console.log(array_foo.join(''), '**')
		this.getMenuData()
	},
	methods: {
		saveUser () {
			var _this = this
			this.$axios({
				 type: '$axios',
				 obj: {
				  url: _this.api.createuser,
				  method: 'post',
				  success (res) {
				    if (res.data.code == 200) {
				      _this.user.addUser = false
				      _this.alertsu('添加用户成功！')
				    } else {
				      _this.alerter(res.data.msg)
				    }
				  },
				  error () {
				    _this.user.addUser = false
				    _this.alerter('添加用户失败！')
				  },
				  data: _this.user,
				  header: {
				    'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
				    'content-type': 'application/json; charset=utf-8'
				  }
				}
			})
		},
		createUser () {
			var _this = this
			_this.user.addUser = true
		},
		handleClose () {
			var _this = this
			_this.user.addUser = false
		},
		submitUser () {
			var _this = this
			_this.$refs.ruleForm.validate((valid) => {
				if (valid) {
				  _this.saveUser()
				} else {
					return false
				}
			})
			
		},
		getMenuData () {
	      var _this = this
	      _this.$get({
	        type: '$get',
	        obj: {
	          url: _this.api.getmenu,
	          success: function (res) {
	            if (res.status == 200) {
	              _this.alertsu('获取菜单数据成功！','获取菜单数据',2000)
	              var treedata = _this.tree1(res.data, _this.menuTreeData)
	              var treedata2 = _this.data2treeDG(treedata[0], treedata[1], _this.menuTreeData)
	              _this.menudata = treedata2
	              console.log(_this.menudata)
	            }
	            else {
	              _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
	            }
	          },
	          error: function () {
	            _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
	          }
	        },
	        param: {
	          id: 1
	        }
	      })
	    },
	    getJurisdiction (obj,checked, item) {
	    	var _this = this
	    	var menudata = this.$refs.treeid.getAllCheckedNodes()
	    	var setmenudata = this.$refs.treeid.getCheckedNodes()
	    	menudata.forEach(function(item){
	    		delete item.children
	    	})
	    	var treedata = _this.tree1(menudata)
	    	var treedata2 = _this.data2treeDG(treedata[0], treedata[1])
	    	_this.user.jurisdiction = JSON.stringify(treedata2)
	    	_this.user.user_menu_jurisdiction_set = JSON.stringify(setmenudata)
	    }
	}
}
</script>


